<template>
  <div>
    <el-dialog
      :title="modifyTitle"
      :visible.sync="v"
      :before-close="handleClose"
      width="520px"
      :close-on-click-modal="false"
      append-to-body
      top="5vh"
    >
      <el-form
        ref="paramFormRef"
        :model="paramForm"
        :rules="paramRules"
        size="small"
        class="demo-ruleForm"
      >
        <el-form-item :label="$t('accountMag.account')" prop="userCode">
          <el-input
            v-model="paramForm.userCode"
            :disabled="!!userId"
            :placeholder="inputPh('accountMag.account')"
          ></el-input>
        </el-form-item>
        <el-form-item :label="$t('accountMag.userName')" prop="userName">
          <el-input
            v-model="paramForm.userName"
            :placeholder="inputPh('accountMag.userName')"
          ></el-input>
        </el-form-item>
        <el-form-item :label="$t('accountMag.roleId')" prop="roleId">
          <ct-async-select
            v-model="paramForm.roleId"
            url="/role/queryRoleByUserId"
            method="get"
            label-key="roleName"
            value-key="roleId"
            :placeholder="selectPh('accountMag.roleId')"
          ></ct-async-select>
        </el-form-item>
        <!-- <el-form-item :label="$t('accountMag.mobile')" prop="mobile">
          <el-input
            v-model="paramForm.mobile"
            :placeholder="inputPh('accountMag.mobile')"
          >
            <ct-select
              slot="prepend"
              v-model="paramForm.areaCode"
              :placeholder="$t('accountMag.areaCode')"
              :options="areaCodeOptions"
              filterable
              style="width: 80px"
            >
            </ct-select>
          </el-input>
        </el-form-item> -->
        <el-form-item :label="$t('accountMag.email')" prop="email">
          <el-input
            v-model="paramForm.email"
            :disabled="!!userId"
            :placeholder="inputPh('accountMag.email')"
          ></el-input>
        </el-form-item>
        <el-form-item :label="$t('accountMag.phone')" prop="phone">
          <el-input
            v-model="paramForm.phone"
            :placeholder="inputPh('accountMag.phone')"
          ></el-input>
        </el-form-item>
        <el-form-item :label="$t('accountMag.fax')" prop="fax">
          <el-input
            v-model="paramForm.fax"
            :placeholder="inputPh('accountMag.fax')"
          ></el-input>
        </el-form-item>
        <el-form-item :label="$t('common.remark')" prop="remark">
          <el-input
            v-model="paramForm.remark"
            type="textarea"
            :rows="4"
            :placeholder="inputPh('accountMag.account')"
            maxlength="20"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button type="primary" size="small" @click="clickOk">{{
          $t('common.ok')
        }}</el-button>
        <el-button size="small" @click="cancel">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
export default {
  mixins: [langMixin],
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    modifyTitle: {
      type: String,
      default: '',
    },
    userId: {
      type: String,
      default: '',
    },
    modifyFlag: {
      type: [Number, String],
      default: 0, // 1编辑 其他 0
    },
  },
  data() {
    return {
      paramForm: {},
      defaultForm: {
        // areaCode: '',
        email: '',
        fax: '',
        // mobile: '',
        phone: '',
        remark: '',
        roleId: '',
        userCode: '',
        userName: '',
        userId: '',
      },
      areaCodeOptions: [],
      statusCd: '1000',
      paramRules: {
        userCode: [{ required: true, validator: this.validateUserCode, trigger: 'blur' }],
        userName: [
          {
            required: true,
            min: 1,
            max: 32,
            message: this.$t('accountMag.userName32'),
            trigger: 'blur',
          },
        ],
        email: [
          {
            required: true,
            type: 'email',
            message: this.inputPh('accountMag.email'),
            trigger: 'blur',
          },
        ],
        mobile: [{ required: true, validator: this.validatePhone, trigger: 'blur' }],
        roleId: [
          {
            required: true,
            message: this.selectPh('accountMag.roleId'),
            trigger: 'blur',
          },
        ],
      },
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },
  watch: {
    visible(val) {
      if (val) {
        if (!this.userId) {
          this.paramForm = { ...this.defaultForm }
        } else {
          this.queryDetail(this.userId)
        }
      }
    },
  },
  mounted() {
    // this.queryAreaCodeList()
  },
  methods: {
    // async queryAreaCodeList() {
    //   const res = await fetch('/user/areaCodeList', {}, { method: 'get' })
    //   this.areaCodeOptions = res.map((item) => {
    //     return {
    //       value: item,
    //       label: item,
    //     }
    //   })
    // },
    async queryDetail(userId) {
      const res = await fetch(
        '/user/querySystemUserDetail',
        {
          userId,
          modifyFlag: this.modifyFlag,
        },
        { method: 'get' }
      )
      const obj = { ...res }
      if (obj.emailDesc) {
        obj.email = obj.emailDesc
      }
      if (obj.faxDesc) {
        obj.fax = obj.faxDesc
      }
      if (obj.mobileDesc) {
        obj.mobile = obj.mobileDesc
      }
      if (obj.phoneDesc) {
        obj.phone = obj.phoneDesc
      }
      if (obj.userNameDesc) {
        obj.userName = obj.userNameDesc
      }
      this.paramForm = { ...obj }
    },
    clickOk() {
      this.$refs.paramFormRef.validate((valid) => {
        if (!valid) return
        const msg = this.userId
          ? this.$t('accountMag.modifySuccess')
          : this.$t('accountMag.createSuccess')
        fetch('/user/saveUser', { ...this.paramForm }, { method: 'post' }).then((res) => {
          this.$refs.paramFormRef.clearValidate()
          this.$message.success(msg)
          this.v = false
          this.$emit('modifyParam', false)
        })
      })
    },
    cancel() {
      this.v = false
      this.$refs.paramFormRef.clearValidate()
    },
    handleClose() {
      this.cancel()
    },
    validateUserCode(rule, value, callback) {
      // 校验函数，确保输入值为6到32位，且至少包含一个字母和一个数字
      if (!/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d_]{6,32}$/.test(value)) {
        callback(new Error(this.$t('accountMag.userCode632')))
        return
      }

      callback()
    },
    validatePhone(rule, value, callback) {
      if (!this.paramForm.areaCode) {
        callback(new Error(this.selectPh('accountMag.areaCode')))
      }
      if (!/^\d{1,15}$/.test(value)) {
        callback(new Error(this.$t('accountMag.correctMobile')))
      }
      callback()
    },
    // validateEmail(rule, value, callback) {
    //   if (!(value.includes('@') && value.includes('.') && value.length < 61)) {
    //     callback(new Error('请输入正确的邮箱地址'))
    //   }
    //   callback()
    // },
    // validatePassword(rule, value, callback) {
    //   if (!value) {
    //     callback(new Error('请输入登录密码'))
    //     return
    //   }
    //   const pattern =
    //     /^(?![A-z0-9]+$)(?=.[^%&',;=?$\x22])(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,20}$/

    //   if (!pattern.test(value)) {
    //     callback(new Error('请输入8位到20位包含英文大小写,数字,特殊字符'))
    //     return
    //   }
    //   callback()
    // },
    // validatePassword2(rule, value, callback) {
    //   if (!value) {
    //     callback(new Error('请输入登录密码'))
    //     return
    //   }
    //   const pattern =
    //     /^(?![A-z0-9]+$)(?=.[^%&',;=?$\x22])(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,20}$/

    //   if (!pattern.test(value)) {
    //     callback(new Error('请输入8位到20位包含英文大小写,数字,特殊字符'))
    //     return
    //   }
    //   if (value != this.paramForm.password) {
    //     callback(new Error('密码不一致'))
    //     return
    //   }
    //   callback()
    // },
  },
}
</script>
<style lang="scss" scoped></style>
