<template>
  <div class="container">
    <div class="container-form">
      <el-form ref="qryParamRef" :model="qryParam" size="small">
        <el-row :gutter="20">
          <el-col :span="5">
            <el-form-item :label="$t('accountMag.account')" prop="userCode">
              <el-input
                v-model="qryParam.userCode"
                :placeholder="inputPh('accountMag.account')"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item :label="$t('accountMag.userName')" prop="userName">
              <el-input
                v-model="qryParam.userName"
                :placeholder="inputPh('accountMag.userName')"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item :label="$t('common.status')" prop="statusCd">
              <ct-select
                v-model="qryParam.statusCd"
                :placeholder="selectPh('common.status')"
                :options="statusCdOption"
              />
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item :label="$t('accountMag.createTime')" prop="qryTime">
              <ct-date-picker
                :begin.sync="qryParam.createTimeBegin"
                :end.sync="qryParam.createTimeEnd"
              >
              </ct-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="4">
            <el-form-item label-width="0">
              <div class="search-button-group">
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  @click="qryCmpParamPage(false)"
                  >{{ $t('common.query') }}
                </el-button>
                <el-button icon="el-icon-refresh" @click="reset">{{
                  $t('common.reset')
                }}</el-button>
              </div>
            </el-form-item>
          </el-col>
        </el-row>
        <div v-compPriv="41" class="tool-button-group">
          <el-button type="primary" icon="el-icon-plus" @click="add">{{
            $t('accountMag.createAccount')
          }}</el-button>
          <el-button v-compPriv="46" icon="el-icon-view" @click="viewPlain">
            {{ $t('accountMag.viewPlain') }}</el-button
          >
        </div>
      </el-form>
      <el-table
        ref="table"
        v-loading="loading"
        class="container-table"
        :data="tableData"
        :border="true"
        overflow-x="scroll"
        height="100%"
      >
        <el-table-column
          prop="userCode"
          :label="$t('accountMag.userCode')"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <a class="cor-blue" @click="toDetail(scope.row)">{{ scope.row.userCode }}</a>
          </template>
        </el-table-column>
        <el-table-column
          prop="userName"
          :label="$t('accountMag.userName')"
          show-overflow-tooltip
        ></el-table-column>
        <!-- <el-table-column
          prop="mobile"
          :label="$t('accountMag.mobile')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column> -->
        <el-table-column
          prop="email"
          :label="$t('accountMag.email')"
          show-overflow-tooltip
        ></el-table-column>
        <!-- <el-table-column
          prop="roleName"
          label="用户角色"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column> -->
        <el-table-column
          prop="statusCd"
          :label="$t('common.status')"
          :formatter="rowFomatter"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="createTime"
          :label="$t('accountMag.createTime')"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column :label="$t('common.action')" fixed="right">
          <template slot-scope="scope">
            <div class="action-button-group">
              <el-button v-compPriv="42" type="text" @click="modify(scope.row)">{{
                $t('common.edit')
              }}</el-button>
              <el-button
                v-if="scope.row.statusCd === '1000'"
                v-compPriv="45"
                type="text"
                @click="disableCmpParam(scope.row.userId)"
                >{{ $t('accountMag.stop') }}</el-button
              >
              <el-button
                v-else
                v-compPriv="45"
                type="text"
                @click="enableCmpParam(scope.row.userId)"
                >{{ $t('accountMag.activate') }}</el-button
              >
              <el-button
                v-compPriv="43"
                type="text"
                class="cor-red"
                @click="clickDelete(scope.row.userId)"
                >{{ $t('common.delete') }}</el-button
              >
            </div>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        v-show="tableData.length > 0"
        background
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
    <AddEditDialog
      :visible.sync="addEditDialogVisible"
      :modify-title="modifyTitle"
      :modify-flag="modifyFlag"
      :user-id="userId"
      @modifyParam="qryCmpParamPage($event)"
    ></AddEditDialog>
    <ViewPlainDialog :visible.sync="plainDialogVisible"></ViewPlainDialog>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
import AddEditDialog from './dialogs/AddEditDialog.vue'
import ViewPlainDialog from './dialogs/ViewPlainDialog.vue'
export default {
  components: {
    AddEditDialog,
    ViewPlainDialog,
  },
  mixins: [langMixin],
  data() {
    return {
      powerSearch: false,
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      channelIdOptions: [],
      roleIdOptions: [],
      tableData: [],
      modifyTitle: '新建',
      modifyFlag: '',
      addEditDialogVisible: false,
      plainDialogVisible: false,
      statusCd: '1000',
      selectUserVisible: false,
      selectCustomerVisible: false,
      qryParam: {
        userCode: '',
        statusCd: '',
        userName: '',
        createTimeBegin: '', // 开始时间
        createTimeEnd: '', // 结束时间
      },
      saleList: [],
      userId: '',
      pickerOptions: {
        disabledDate(time) {
          return (
            time.getTime() < Date.now() - 30 * 24 * 60 * 60 * 1000 ||
            time.getTime() > Date.now()
          )
        },
      },
    }
  },
  computed: {
    statusCdOption() {
      return [
        {
          value: '1000',
          label: this.$t('simMag.statusCdGood'),
        },
        {
          value: '1100',
          label: this.$t('simMag.statusCdBad'),
        },
      ]
    },
  },
  mounted() {
    this.qryCmpParamPage(false)
  },
  methods: {
    async queryRoleList() {
      this.qryParam.roleId = ''
      this.roleIdOptions = []
      const res = await fetch(
        '/sysrole/queryRoleList',
        { channelId: this.qryParam.channelId || 0, custId: this.qryParam.custId },
        { method: 'post' }
      )
      this.roleIdOptions = res.map((item) => {
        return {
          value: item.roleId,
          label: item.roleName,
        }
      })
    },
    qryCmpParamPage(pageChangeFlag) {
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
        this.page.pageSize = 10
      }
      const param = {
        ...this.qryParam,
        ...this.page,
      }
      this.loading = true
      this.$api.custMag
        .queryUser(param)
        .then((res) => {
          res.list.forEach((obj) => {
            if (obj.emailDesc) {
              obj.email = obj.emailDesc
            }
            if (obj.faxDesc) {
              obj.fax = obj.faxDesc
            }
            if (obj.mobileDesc) {
              obj.mobile = obj.mobileDesc
            }
            if (obj.phoneDesc) {
              obj.phone = obj.phoneDesc
            }
            if (obj.userNameDesc) {
              obj.userName = obj.userNameDesc
            }
          })
          this.tableData = res.list
          this.page.total = res.total
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.qryCmpParamPage(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.qryCmpParamPage(true)
    },
    add() {
      this.addEditDialogVisible = true
      this.modifyTitle = this.$t('accountMag.createAccount')
      this.modifyFlag = 0
      this.userId = null
    },
    modify(row) {
      this.addEditDialogVisible = true
      this.modifyTitle = this.$t('accountMag.editAccount')
      this.modifyFlag = 1
      this.userId = row.userId
    },
    toDetail({ userId }) {
      this.$router.push({ path: 'accountDetail', query: { userId } })
    },
    viewPlain() {
      this.plainDialogVisible = true
    },
    // 修改
    modifyParam(value) {
      if (value) {
        this.userId = null
        this.qryCmpParamPage(false)
      }
    },
    clickDelete(userId) {
      this.$confirm(this.$t('accountMag.deleteAsk'), {
        confirmButtonText: this.$t('common.yes'),
        cancelButtonText: this.$t('common.no'),
        type: 'warning',
      })
        .then(() => {
          this.loading = true
          fetch('/user/removeSystemUser', { userId }, { method: 'get' })
            .then((res) => {
              this.$message({
                type: 'success',
                message: this.$t('common.deleteSuccess'),
              })
              this.qryCmpParamPage(false)
            })
            .finally(() => {
              this.loading = false
            })
        })
        .catch(() => {})
    },
    async changeStatus(userId, statusCd) {
      try {
        await fetch(
          '/user/changeStatus',
          {
            userId,
            statusCd,
          },
          { method: 'get' }
        )
        const message =
          statusCd === '1000'
            ? this.$t('accountMag.activateSuccess')
            : this.$t('accountMag.stopSuccess')
        this.$message({
          type: 'success',
          message,
        })
        this.qryCmpParamPage(true)
      } catch (err) {
        console.log(err)
      }
    },
    // 启用
    enableCmpParam(userId) {
      const that = this
      this.$confirm(
        this.$t('accountMag.activateAsk'),
        this.$t('accountMag.activateAccount'),
        {
          confirmButtonText: this.$t('common.yes'),
          cancelButtonText: this.$t('common.no'),
          type: 'success',
        }
      )
        .then(async () => {
          await that.changeStatus(userId, '1000')
        })
        .catch(() => {})
    },
    // 停用
    disableCmpParam(userId) {
      this.$confirm(this.$t('accountMag.stopAsk'), this.$t('accountMag.stopAccount'), {
        confirmButtonText: this.$t('common.yes'),
        cancelButtonText: this.$t('common.no'),
        type: 'warning',
      })
        .then(() => {
          this.changeStatus(userId, '1100')
        })
        .catch(() => {})
    },
    // {{ $t('common.reset') }}
    reset() {
      this.$refs.qryParamRef.resetFields()
      this.qryCmpParamPage(false)
    },
    rowFomatter(row, col, val) {
      if (col.property === 'statusCd') {
        return val === '1000'
          ? this.$t('simMag.statusCdGood')
          : this.$t('simMag.statusCdBad')
      }
    },
  },
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
.action-button-group {
  display: flex;
  .el-button:first-child {
    padding-left: 0;
  }
}
</style>
