<template>
  <div>
    <el-dialog
      :title="$t('api.viewPlain')"
      :visible.sync="v"
      :before-close="handleClose"
      width="520px"
      :close-on-click-modal="false"
      append-to-body
    >
      <el-form ref="paramFormRef" :model="paramForm" :rules="paramRules" size="small">
        <el-form-item :label="$t('api.currentEmail')" prop="email">
          <el-input v-model="paramForm.email" disabled></el-input>
        </el-form-item>
        <el-form-item :label="$t('api.checkCode')" prop="verifyCode">
          <el-row :gutter="10">
            <el-col :span="19">
              <el-input
                v-model="paramForm.verifyCode"
                :placeholder="inputPh('api.checkCode')"
              ></el-input>
            </el-col>
            <el-col :span="3">
              <el-button
                v-if="!countingDown"
                key="1"
                type="primary"
                size="small"
                @click="sendSms"
                >{{ $t('api.sendCode') }}</el-button
              >
              <el-button v-else key="2" disabled>{{
                $t('login.countDown', { countDownNum: countDownTime })
              }}</el-button>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" @click="clickOk">{{
          $t('common.ok')
        }}</el-button>
        <el-button size="small" @click="cancel">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
export default {
  mixins: [langMixin],
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    modifyForm: {
      type: Object,
      default: () => {
        return null
      },
    },
  },
  data() {
    return {
      countDownTime: 60,
      countingDown: false,
      paramForm: {},
      defaultForm: { email: this.$store.state.user.user.email, verifyCode: '' },
      paramRules: {
        email: [
          { required: true, message: this.inputPh('accountMag.email'), trigger: 'blur' },
        ],
        verifyCode: [{ required: true, message: this.inputPh('api.checkCode') }],
      },
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },
  watch: {
    visible(val) {
      if (val) {
        if (this.modifyForm) {
          this.paramForm = { ...this.modifyForm }
        } else {
          this.paramForm = { ...this.defaultForm }
        }
        this.$nextTick(() => {
          this.$refs.paramFormRef.clearValidate()
        })
      }
    },
  },

  mounted() {},
  methods: {
    clickOk() {
      this.$refs.paramFormRef.validate((valid) => {
        if (!valid) return
        fetch(
          '/user/viewPlainText',
          { ...this.paramForm, sceneCode: 'view_plain_text' },
          { method: 'get' }
        ).then((res) => {
          this.$refs.paramFormRef.resetFields()
          this.$message.success(this.$t('accountMag.setSuccess'))
          this.v = false
          this.$emit('modifyParam', true)
        })
      })
    },
    async sendSms() {
      await fetch('/user/sendMail', { sceneCode: 'view_plain_text' }, { method: 'get' })
      this.$message.success(this.$t('accountMag.sendSuccess'))
      this.startCountDown()
    },
    cancel() {
      this.resetCountDown()
      this.$refs.paramFormRef.clearValidate()
      this.v = false
    },
    handleClose() {
      this.cancel()
    },
    startCountDown() {
      this.countingDown = true
      const countDownInterval = setInterval(() => {
        if (this.countDownTime > 1) {
          this.countDownTime--
        } else {
          clearInterval(countDownInterval)
          this.resetCountDown()
        }
      }, 1000)
    },
    resetCountDown() {
      this.countingDown = false
      this.countDownTime = 60
    },
  },
}
</script>
<style lang="scss" scoped></style>
